
$blue_1: #3498db;


$large-device : 1100px;
$medium-device : 765px;


@media screen and (min-width: $medium-device){
  .full-bg-section {
    width: 100%;
    height: 500px;
    background-image: url('img/Polygon-Flat-Backgrounds.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    background-attachment: fixed;
    margin-top: 60px;
  }
}

@media screen and (max-width: $medium-device){
  .full-bg-section {
    width: 100%;
    height: 300px;
    background-image: url('img/Polygon-Flat-Backgrounds.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    background-attachment: fixed;
  }
}


.responsive-container {
  width: 1040px;
}

.profile-panel {
  margin-top: 21px;
}

.font-14 {
  font-size: 14px;
}

.section-vertical-padding {
  padding-top: 40px;
  padding-bottom: 40px;
}

.profile-block {
  padding-bottom: 40px;
}

@media screen and (max-width: $medium-device) {
  .profile-block {
    padding-bottom: 20px;
  }
}

.body--with-nav {
  padding-top: 60px;
}
